.trivia {
    transition: all 0.5s;
    text-align: center;
    background: lightgreen;
    display: block;
    border: 3px;
    padding: 3px;
    margin: 5px;
    border-width: 2px;
    box-shadow: 5px;
    height: 20vh;
    font-size: 0.8em;
    font-family: 'Libre Baskerville', serif;
    line-height: 3.5vh;
    border-radius: 5px;
    box-shadow: 4px 4px 5px #112203;
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
}

.trivia:hover {
    transform: scale(1.015);
}